import React from 'react';
import FooterElement from './FooterElement/FooterElement';

import Link from 'next/link'
import { Layout, Menu, Breadcrumb, Col, Row } from "antd";
const { Header, Content, Footer } = Layout;

export default class AppLayout extends React.Component {
    state = {
        current: 'home',
        pages: [
            {page: 'About', subs: ['Start + Future']},
            {page: 'Labs', subs: ['Ed Labs', 'Dev Labs']},
            {page: 'Projects', subs: ['Purplished']},
            {page: 'Join', subs: ['']},
            {page: 'Contact', subs: ['']},
            {page: 'Team', subs: ['']}
        ]
    }
    handleClick = (event) => {
        this.setState({
          current: event.key,
        });
      }
    render() {
        let footerLinks = this.state.pages.map((page, i) => {
            return(
                <Col xs={24} sm={8} md={8} lg={4} xl={4} style={{margin: 'auto', padding:'0.6em'}}>
                    <FooterElement key={i} page={page['page']}>
                     {page['subs']}
                    </FooterElement>
                </Col>
                
            )
        }
            
        )
        return(
            <Layout>
                <div>
                    <div className="logo" />
                    <Menu
                    theme="light"
                    onClick={this.handleClick}
                    selectedKeys={this.state.current}
                    mode="horizontal"
                    style={{ lineHeight: '64px' }}>
                        <Menu.Item key="home">
                            <Link href="/">Home</Link>
                        </Menu.Item>
                        <Menu.Item key="about">
                            <Link href="/about">About</Link>
                        </Menu.Item>
                        <Menu.Item key="labs">
                            <Link href="/labs"> Labs</Link>
                        </Menu.Item>
                        <Menu.Item key="projects">
                            <Link href="/projects">Projects</Link>
                        </Menu.Item>
                        <Menu.Item key="blog">
                            <Link href="/blog">Blog</Link>
                        </Menu.Item>
                        <Menu.Item key="team">
                            <Link href="/team">Team</Link>
                        </Menu.Item>
                    </Menu>
                </div>
                <Content style={{padding: '4em 2em 4em', backgroundColor: 'white'}}>
                        {this.props.children}
                </Content>
                <Footer style={{backgroundColor:' #b2bec3'}}>
                    <Row align="top" justify="center">
                        {footerLinks}
                    </Row>
                </Footer>
            </Layout>
        )
       
    }
}


